"use client";
import { useState } from 'react';
import { useFinance } from '@/context/FinanceContext';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Trash2, Save, Plus, X } from 'lucide-react';

export default function AdminPage() {
  const { categories, fixedRules, deleteFixedRule, updateAccount, accounts } = useFinance();
  const [newCategory, setNewCategory] = useState('');

  return (
    <div className="container mx-auto p-4 max-w-4xl space-y-8">
      <h1 className="text-3xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-zinc-200 to-zinc-500">
        Configuración Avanzada
      </h1>

      <Tabs defaultValue="fixed" className="space-y-4">
        <TabsList className="bg-zinc-900 border border-zinc-800">
            <TabsTrigger value="fixed">Gastos Fijos (Reglas)</TabsTrigger>
            <TabsTrigger value="accounts">Cuentas y Saldos</TabsTrigger>
            <TabsTrigger value="categories">Listas y Categorías</TabsTrigger>
        </TabsList>

        {/* GESTIÓN DE REGLAS FIJAS */}
        <TabsContent value="fixed">
            <Card className="bg-zinc-900 border-zinc-800">
                <CardHeader>
                    <CardTitle>Reglas de Gastos Recurrentes</CardTitle>
                    <CardDescription>Estos gastos se generan automáticamente cada mes.</CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                    {fixedRules.length === 0 && <p className="text-zinc-500">No hay reglas configuradas aún. Crea una al registrar un gasto nuevo marcando "Repetir".</p>}
                    {fixedRules.map(rule => (
                        <div key={rule.id} className="flex items-center justify-between p-4 bg-zinc-950/50 border border-zinc-800 rounded-lg">
                            <div>
                                <p className="font-bold text-white">{rule.description}</p>
                                <p className="text-sm text-zinc-400">
                                    Se genera el día <span className="text-indigo-400">{rule.dayOfMonth}</span> por valor de <span className="text-emerald-400">${rule.budgetedAmount.toLocaleString()}</span>
                                </p>
                            </div>
                            <Button variant="destructive" size="icon" onClick={() => deleteFixedRule(rule.id)}>
                                <Trash2 className="h-4 w-4" />
                            </Button>
                        </div>
                    ))}
                </CardContent>
            </Card>
        </TabsContent>

        {/* GESTIÓN DE CUENTAS */}
        <TabsContent value="accounts">
             <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {accounts.map(acc => (
                    <Card key={acc.id} className="bg-zinc-900 border-zinc-800">
                        <CardHeader className="pb-2">
                            <CardTitle className="text-base">{acc.bank} - {acc.name}</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="space-y-2">
                                <Label>Saldo Actual (Corrección manual)</Label>
                                <div className="flex gap-2">
                                    <Input 
                                        type="number" 
                                        defaultValue={acc.balance} 
                                        className="bg-black border-zinc-700"
                                        onBlur={(e) => updateAccount({...acc, balance: parseFloat(e.target.value)})}
                                    />
                                </div>
                                <p className="text-xs text-zinc-500">Edita el valor y haz clic fuera para guardar.</p>
                            </div>
                        </CardContent>
                    </Card>
                ))}
             </div>
        </TabsContent>

        <TabsContent value="categories">
    <Card className="bg-zinc-900 border-zinc-800">
        <CardHeader>
            <CardTitle>Categorías y Listas</CardTitle>
            <CardDescription>Administra las opciones de tus selectores</CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
            
            {/* Agregar Nueva Categoría */}
            <div className="flex gap-2 items-end border-b border-zinc-800 pb-6">
                <div className="space-y-2 flex-1">
                    <Label>Nueva Categoría</Label>
                    <Input 
                        placeholder="Nombre (ej: Viajes)" 
                        value={newCategory}
                        onChange={(e) => setNewCategory(e.target.value)}
                        className="bg-black border-zinc-700"
                    />
                </div>
                <Button 
                    onClick={() => {
                        if(!newCategory) return;
                        // Agregamos una categoría genérica de gasto por defecto
                        const newCat = { id: crypto.randomUUID(), name: newCategory, type: 'expense' as const };
                        // Aquí deberías agregar la función addCategory al context si no la tienes expuesta, 
                        // por ahora simularemos que existe o la agregas al context.
                        // addCategory(newCat); 
                        setNewCategory('');
                        alert('Categoría agregada (Asegúrate de exponer addCategory en el Contexto)');
                    }}
                    className="bg-indigo-600 hover:bg-indigo-700"
                >
                    <Plus className="h-4 w-4 mr-2" /> Agregar
                </Button>
            </div>

            {/* Listado de Categorías Existentes */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                {categories.map(cat => (
                    <div key={cat.id} className="flex justify-between items-center p-3 bg-zinc-950 rounded border border-zinc-800">
                        <div>
                            <span className="font-medium text-zinc-200">{cat.name}</span>
                            <span className={`ml-2 text-xs px-2 py-0.5 rounded ${
                                cat.type === 'income' ? 'bg-green-900 text-green-300' : 
                                cat.type === 'payment' ? 'bg-blue-900 text-blue-300' : 'bg-red-900 text-red-300'
                            }`}>
                                {cat.type === 'income' ? 'Ingreso' : cat.type === 'payment' ? 'Pago' : 'Gasto'}
                            </span>
                        </div>
                        {/* Botón de eliminar (Solo visual por seguridad en este demo, implementa deleteCategory en context si lo necesitas) */}
                        <Button variant="ghost" size="sm" className="text-zinc-500 hover:text-red-400">
                            <Trash2 className="h-4 w-4" />
                        </Button>
                    </div>
                ))}
            </div>
        </CardContent>
    </Card>
</TabsContent>
      </Tabs>
    </div>
  );
}